import React, { Component } from "react";
import wishlistItems from "../wishlistItems";
import WishlistItem from "./WishlistItem";

class Wishlist extends Component {
  wishlistItems: any[];
  state = {
    meta: {},
    data: [],
    width: window.innerWidth,
    renderTablet: false
  }

  constructor(props: any) {
    super(props);
    this.wishlistItems = wishlistItems;
  }

  componentWillMount() {
    window.addEventListener("resize", this.handleWindowSizeChange);
    this.handleWindowSizeChange();
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.handleWindowSizeChange);
  }

  handleWindowSizeChange = () => {
    this.setState({ width: window.innerWidth });
    const { width } = this.state;
    var renderTablet = width <= 1024;
    this.setState({ renderTablet });
  };

  render() {   
    var pictureHeaderRow, pictureRow;
    pictureHeaderRow = <div className="wishlist-header-col">Picture</div>;
    if(this.state.renderTablet)
      pictureHeaderRow = "";
    return (
      <div id="wishlistContainer">
        <h1>Cortney's Dope Wishlist</h1>
        <div className="wishlist-header-row">
          <div className="wishlist-header-col"></div>
          {pictureHeaderRow}
          <div className="wishlist-header-col">Name</div>
          <div className="wishlist-header-col">Link to Buy</div>
          <div className="wishlist-header-col">Price</div>
          <div className="wishlist-header-col">Details</div>
        </div>
        {Object.keys(this.wishlistItems).map(key => (          
              <WishlistItem
                renderTablet={this.state.renderTablet}
                key={key}
                index={key}
                details={this.wishlistItems[parseInt(key)]}
              />
            ))}
      </div>
    );
  }
}

export default Wishlist;